/* General styling */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7fc;
}

/* Home page container */
.home-page {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  text-align: center;
}

/* Header */
.home-page h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  font-weight: bold;
}

/* Dropdown and Button Container */
.home-page select,
.home-page button {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  color: #333;
}

.home-page select {
  background-color: #f9f9f9;
}

.home-page button {
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: bold;
}

.home-page button:hover {
  background-color: #45a049;
}

.view-prices-button {
  background-color: #007bff;
}

.view-prices-button:hover {
  background-color: #0056b3;
}

/* Responsive styling */
@media (max-width: 480px) {
  .home-page {
    width: 90%;
    padding: 15px;
  }
  
  .home-page h1 {
    font-size: 1.8rem;
  }

  .home-page select,
  .home-page button {
    font-size: 0.9rem;
    padding: 10px;
  }
}
