/* Container styling */
.view-prices-page {
  font-family: 'Arial', sans-serif;
  padding: 20px;
  background-color: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Title styling */
.view-prices-page h1 {
  color: #333;
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Error message styling */
.error-message {
  color: red;
  font-weight: bold;
  margin-bottom: 20px;
}

/* User prices list styling */
ul {
  list-style-type: none;
  padding: 0;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
}

ul li {
  background-color: #fff;
  padding: 15px 20px;
  margin-bottom: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1rem;
  color: #333;
}

/* Reset button styling */
.reset-prices-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 20px;
}

.reset-prices-button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.reset-prices-button:active {
  transform: scale(1);
}

/* Back button styling */
.back-button {
  background-color: #6c757d;
  color: #fff;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 10px;
}

.back-button:hover {
  background-color: #5a6268;
  transform: scale(1.05);
}

.back-button:active {
  transform: scale(1);
}



/* Styling for each user price item */
.user-price-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 8px;
}

/* Styling for the individual reset button */
.reset-user-price-button {
  background-color: #dc3545;
  color: #fff;
  padding: 5px 10px;
  font-size: 0.9rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.reset-user-price-button:hover {
  background-color: #c82333;
  transform: scale(1.05);
}

.reset-user-price-button:active {
  transform: scale(1);
}

/* Styling for the back button */
.back-button {
  margin-top: 20px;
  background-color: #6c757d;
  color: #fff;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.back-button:hover {
  background-color: #5a6268;
  transform: scale(1.05);
}

.back-button:active {
  transform: scale(1);
}
