/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7fc;
}

/* User Page Container */
.user-page {
  max-width: 500px;
  margin: 50px auto;
  padding: 30px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Header */
.user-page h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  font-weight: bold;
}

/* User Details Card */
.user-details-card {
  margin-bottom: 30px;
  text-align: center;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.user-details-card p {
  font-size: 1.1rem;
  color: #555;
}

.user-details-card strong {
  font-weight: bold;
  color: #4CAF50;
}

/* Error Message */
.error-message {
  color: red;
  font-size: 1rem;
  margin-bottom: 20px;
  text-align: center;
}

/* Form Container */
.form-container {
  margin-top: 30px;
}

/* Form Group */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 1rem;
  margin-bottom: 8px;
  color: #333;
}

.form-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  color: #333;
  background-color: #f9f9f9;
  box-sizing: border-box;
}

.form-group input:focus {
  border-color: #4CAF50;
  outline: none;
}

/* Buttons */
button {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  transition: all 0.3s ease;
}

.submit-button {
  background-color: #4CAF50;
  color: white;
  font-weight: bold;
}

.submit-button:hover {
  background-color: #45a049;
}

.back-button {
  margin-top: 20px;
  background-color: #007bff;
  color: white;
  font-weight: bold;
}

.back-button:hover {
  background-color: #0056b3;
}

/* Responsive Design */
@media (max-width: 480px) {
  .user-page {
    padding: 20px;
    margin: 20px;
  }

  .user-page h1 {
    font-size: 1.8rem;
  }

  .user-details-card p {
    font-size: 1rem;
  }

  .form-group input {
    padding: 10px;
    font-size: 0.9rem;
  }

  button {
    padding: 10px;
    font-size: 0.9rem;
  }
}
